import { tsParticles } from "@tsparticles/engine";
import { loadAll } from "@tsparticles/all";

async function loadParticles(options) {
    await loadAll(tsParticles);

    await tsParticles.load({ id: "tsparticles", options });
}

const configs = {
    particles: {
        number: {
            value: 100,
        },
        color: {
            value: "#fff",
        },

        links: {
            enable: true,    // Enable links between particles
            distance: 200,   // Adjust the distance between connected particles
            color: "#ffffff", // Color of the links
            opacity: 0.5,     // Opacity of the links (0 to 1)
            width: 1,          // Width of the links
        },
        shape: {
            type: "square",
        },
        opacity: {
            value: 0.7,
        },
        size: {
            value: {
                min: 2,
                max: 5,
            }
        },
        move: {
            enable: true,
            speed: 1.5,
        },
    },
    background: {
        color: "#000",
    },
};

loadParticles(configs);

function Render(){
    return(
        <div id={"tsparticles"} style={{zIndex: 0}}></div>
    )
}

export default Render;